@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

* {
	box-sizing: border-box;
	font-family: 'Montserrat', sans-serif;
	margin: 0;
}

.container {
	padding: 0 100px;
}

.app-wrapper {
	margin: 0 auto;
	max-width: 1150px;
	display: grid;
	grid-template-rows: 60px 1fr;
	grid-template-columns: 2fr 10fr;
	grid-template-areas:
		'header header'
		'sidebar content';
	grid-gap: 10px;
}